<template>
  <div class="movie-intro-text_overview---cast">
    <h2 class="movie-intro-text_overview---cast___h2">Casts</h2>
    <Swiper
      :breakpoints="swiperOptions.breakpoints"
      class="movie-intro-text_overview---cast_div"
      :space-between="5"
      @swiper="onSwiper"
      @slideChange="onSlideChange"
    >
      <SwiperSlide
        class="movie-intro-text_overview---cast___actors"
        v-for="(man, index) in actors"
        :key="index"
        @click="getActorId(man)"
      >
        <img
          class="movie-intro-text_overview---cast___actors-img"
          :src="imageUrl + man.profile_path"
          alt=""
          @click="openLoading"
        />
        <p class="movie-intro-text_overview---cast___actors-name">
          {{ man.name }}
        </p>
      </SwiperSlide>
    </Swiper>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from "swiper/vue";
import "swiper/scss";
import "swiper/scss/navigation";
import { mapGetters, mapActions } from "vuex";
export default {
  props: ["id", "type"],
  components: {
    Swiper,
    SwiperSlide,
  },
  data() {
    return {
      clicked: 0,
      swiperOptions: {
        breakpoints: {
          320: { slidesPerView: 3 },
          576: { slidesPerView: 4 },
          768: { slidesPerView: 5 },
          992: { slidesPerView: 5 },
          1200: { slidesPerView: 6 },
        },
      },
      imageUrl: "https://image.tmdb.org/t/p/w500",
      imageFullUrl: "https://image.tmdb.org/t/p/original",
      actors: [],
    };
  },

  computed: {
    ...mapGetters("cast", ["getActorsMovieArray"]),
  },

  methods: {
    ...mapActions("cast", ["getActorsMovie"]),

    getActorId(actor) {
      setTimeout(() => {
        this.$router.push("/actor");
      }, 500);
      localStorage.setItem("actorId", actor.id);
    },
  },
  async mounted() {
    await this.getActorsMovie({ type: this.type, movieId: this.id });
    setTimeout(() => {
      console.log(this.id);
      
      this.actors = this.getActorsMovieArray;
    }, 500);
    ////////////////////////////////////////////////
  },
};
</script>

<style lang="scss" scoped>

.movie-intro-text_overview---cast {
  &___h2 {
    color: white;
  }
  width: 100%;
  display: flex;
  // flex-wrap: wrap;
  align-items: flex-start;
  gap: 5px;
  flex-direction: column;
  &_div {
    width: 100%;
    display: flex;
    gap: 50px;
    // flex-wrap: wrap;
  }

  &___actors {
    // width:100px  ;
    width: 100%;
    height: 200px;
    object-fit: cover;
    padding: 0 0 25px 0;
    // height: 160px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    P {
      font-weight: 400;
      line-height: 1.5rem;
      font-size: 0.8rem;
      color: #fff;
    }
  }
}
.movie-intro-text_overview---cast_div {
  display: flex;
  gap: 10px;
}
.swiper-slide {
  cursor: pointer;
}
.swiper {
  padding: 0 !important;
}
</style>