
import axios from "axios";
export default {
  namespaced: true,
  state: {
    path: "https://api.themoviedb.org/3/movie/",
    movie: null,
  },
  getters: {
    getSimilarArray(state) {
      return state.movie;
    },
  },
  mutations: {
    getSimilar(state, payload) {
      state.movie = payload.splice(0, 10);
    },
  },
  actions: {
    async getSimilar({ state, commit, rootState }, movieId) {
      try {
        const res = await axios.get(
          `${state.path}${movieId}/similar?api_key=${rootState.apiKey}&language=en-US`
        );
        const result = res.data.results;
        const filteredResult = result.filter(element => element.backdrop_path !== null);
        commit("getSimilar", filteredResult);
        // console.log(result);
      } catch (error) {
        console.log(error);
      }
    },
  },
};
