import axios from "axios"
export default {
  namespaced: true,
  state: {
    path: 'https://api.themoviedb.org/3/',
    movie: null
  },
  getters: {
    getMovieObj(state){return state.movie}
  },
  mutations: {
    getMovieId(state, payload){
      state.movie = payload
    }
  },
  actions: {
    async getMovieId({commit, state, rootState}, {type, movieId}){
      try{
        const res = await axios.get(`${state.path}${type}/${movieId}?api_key=${rootState.apiKey}&language=en-US`)
        const result = res.data
        commit('getMovieId', result)
      }catch(err){
        console.log(err);
      }
    }
  },
}