<template>
  <Transition name="upcoming-item">
    <div class="main__upcoming-item" v-if="slideView == index">
      <img
        :src="getFullImage + slide.backdrop_path"
        alt=""
        class="main__upcoming-item-img"
      />
      <div class="main__upcoming-content container">
        <h2 class="main__upcoming-content-title" v-if="slide.title">{{ slide.title }}</h2>
        <h2 class="main__upcoming-content-title" v-else-if="slide.name">{{ slide.name }}</h2>
        <p class="main__upcoming-content-text">{{ slide.overview }}</p>
        <BtnMore @click="logger" :page="slide.media_type" :id="slide.id" :slide="slide" :type="slide.media_type" />
      </div>
      <div class="main__upcoming-next" @click="$emit('nextSlide')">
        <img
          :src="getImage + next.backdrop_path"
          alt=""
          class="main__upcoming-next-img"
        />
        <div class="main__upcoming-next-text">
          <span>Next</span>
          <h4 v-if="next.title">{{ next.title }}</h4>
          <h4 v-if="next.name">{{ next.name }}</h4>
        </div>
        <div class="main__upcoming-next-line"></div>
      </div>
    </div>
  </Transition>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  props: ["index", "slide", "slideView", "next", "key"],
  computed: {
    ...mapGetters(["getFullImage", "getImage"]),
  },
  data() {
    return {
      type: 1,
      page:'movie'
    };
  },
  methods: {
    logger(){
      console.log(this.slide);
    }
  },
};
</script>

