<template>
  <div class="main__movies">
    <router-link to="/films" class="main__movies-title">
      Tv Series
      <fa icon="fa-solid fa-chevron-right" />
    </router-link>

    <Swiper
      :modules="modules"
      :space-between="24"
      :navigation="true"
      :breakpoints="swiperOptions.breakpoints"
    >
      <Swiper-slide
        class="main__movies-item"
        v-for="(slide, index) in moviesList"
        :key="index"
        :slide="slide"
        @click="getTvS(slide, index)"
      >
        <img :src="getImage + slide.poster_path" alt="" />
      </Swiper-slide>
      <Swiper-slide class="main__movies-item">
        <router-link to="/series">
          <fa icon="fa-solid fa-chevron-right" />
          <span>Все Сериалы</span>
        </router-link>
      </Swiper-slide>
    </Swiper>

    <InfoBlockTv
      :movieId="movieId"
      :index="index"
      :movieObj="movieObj"
      @close="movieObj = movieId = null"
    />
  </div>
</template>

<script>
import InfoBlockTv from "@/components/InfoBlockTv.vue";
import { mapActions, mapGetters } from "vuex";
import { Swiper, SwiperSlide } from "swiper/vue";
import { Navigation } from "swiper";
import "swiper/scss";
import "swiper/scss/navigation";

export default {
  components: {
    Swiper,
    SwiperSlide,
    InfoBlockTv,
  },
  data() {
    return {
      modules: [Navigation],
      moviesList: null,
      swiperOptions: {
        breakpoints: {
          320: {
            slidesPerView: 1,
          },
          576: {
            slidesPerView: 2,
          },
          768: {
            slidesPerView: 3,
          },
          992: {
            slidesPerView: 4,
          },
          1200: {
            slidesPerView: 5,
          },
        },
      },
      movieId: null,
      movieObj: null,
      index: null,
      type: "tv",
    };
  },
  methods: {
    ...mapActions("tvs", ["getTv"]),
    ...mapActions("movie", ["getMovieId"]),

    async getTvS(movie, ind) {
      // console.log(movie);
      this.movieId = movie.id;
      this.movieObj = null;
      this.index = ind;
      localStorage.setItem("tvId", this.movieId);
      await this.getMovieId({movieId: this.movieId, type:this.type});
      this.movieObj = this.getMovieObj;
      console.log(this.movieObj);
    },
  },
  computed: {
    ...mapGetters(["getFullImage", "getImage"]),
    ...mapGetters("tvs", ["getTvArray"]),
    ...mapGetters("movie", ["getMovieObj"]),
  },
  async mounted() {
    await this.getTv();
    this.moviesList = this.getTvArray;
  },
};
</script>

