<template>
  <div class="main-content__card" @click="goToMovieOrSerial">
    <img
      src="@/assets/images/no-img.jpg"
      alt=""
      class="main-content__card--img"
      v-if="item.backdrop_path == null"
    />
    <img
      :src="getFullImage + item.backdrop_path"
      alt=""
      class="main-content__card--img"
      v-else
    />
    <p  class="main-content__card--link">{{ item.title }}</p>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  props: ["item"],
  data() {
    return {
      id: null,
    }
  },
  computed: {
    ...mapGetters(["getFullImage"]),
  },
  mounted() {
    this.id = this.item.id
  },
  methods: {
    goToMovieOrSerial() {
      if (this.item.media_type == "movie") {
        localStorage.setItem('movieId', this.item.id)
        this.$router.push(`/movie/${this.id}`);
      }else{
        localStorage.setItem('tvId', this.item.id)
        this.$router.push(`/series/${this.id}`);
      }
    },
  },
};
</script>