import axios from "axios";
export default {
  namespaced: true,
  state: {
    path: "https://api.themoviedb.org/3/person/",
    movie: null,
    movie2:null,
  },
  getters: {
    getActorArray(state) {
      return state.movie;
    },
    getActorMoviesArray(state) {
      return state.movie2;
    },
  },
  mutations: {
    getActor(state, payload) {
      state.movie = payload;
    },
    getActorMovies(state, payload) {
      state.movie2 = payload;
    },
  },
  actions: {
    async getActor({ state, commit, rootState }, personId) {
      try {
        const res = await axios.get(
          `${state.path}${personId}?api_key=${rootState.apiKey}&language=en-US`
        );
        const result = res.data;
        commit("getActor", result);
        // console.log(result);
      } catch (error) {
        console.log(error);
      }
    },
    async getActorMovies({ state, commit, rootState }, personId) {
      try {
        const res = await axios.get(
          `${state.path}${personId}/combined_credits?api_key=${rootState.apiKey}&language=en-US`
        );
        const result = res.data.cast.splice(0, 10);
        commit("getActorMovies", result);
        // console.log(result);
      } catch (error) {
        console.log(error);
      }
    },
  },
};
