import axios from "axios";
export default {
  namespaced: true,
  state: {
    path: "https://api.themoviedb.org/3/",
    movie: null,
  },
  getters: {
    getActorsMovieArray: (state) => state.movie
  },
  mutations: {
    getActorsMovie(state, payload) {
      state.movie = payload.cast.splice(0, 10);
    },
  },
  actions: {
    async getActorsMovie({ state, commit, rootState }, {type, movieId}) {
      try {
        const res = await axios.get(
          `${state.path}${type}/${movieId}/credits?api_key=${rootState.apiKey}&language=en-US`
        );
        const result = res.data;
        commit("getActorsMovie", result);
        // console.log(result);
      } catch (error) {
        console.log(error);
      }
    },
  },
};
