  }
  // margin-top: 100px;
  &--video {
    padding: 20px;
    width: 100%;
    height: 100%;
    border: none;
    outline: none;
  }
}

body {
  background: #0f0f0f;
}
.movie {
  position: relative;
  width: 100%;
  height: 765px;
  overflow: hidden;
  &-videos {
    &__wrapper {
      // max-width: 1400px;
      // margin: 0 auto;

      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      gap: 100px;
    }
    // margin-top: 50px;
    padding: 25px 0 0 0;
    width: 100%;
    height: 100%;
    background: #000;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &-intro {
