import { createRouter, createWebHashHistory } from 'vue-router'

import Home from "@/pages/Home.vue"
import Films from "@/pages/Films.vue"
import Search from "@/pages/Search.vue"
import Series from "@/pages/Series.vue"
import pageMovieId from "@/pages/pageMovieId.vue"
import pageSerialId from "@/pages/pageSerialId.vue"
import Movie from '@/pages/Movie.vue'
import MovieSimilar from '@/pages/MovieSimilar.vue'
import Actor from '@/pages/Actor.vue'
import ActorMovie from '@/pages/ActorMovie.vue'
import MovieCopy from '@/pages/MovieCopy.vue'
const router = createRouter({
  history: createWebHashHistory(),
  routes: [
    {path: '/', name: 'Home', component: Home},
    {path: '/films', name: 'Films', component: Films},
    {path: '/actorMovie', name: 'ActorMovie', component: ActorMovie},
    {path: '/actor', name: 'Actor', component: Actor},
    {path: '/search', name: 'Search', component: Search},
    {path: '/tv', name: 'Series', component: Series},
    {path: '/films/:id', name: 'pageMovieId', component: pageMovieId},
    {path: '/series/:id', name: 'pageSerialId', component: pageSerialId},
    {path: '/movie/:id', name: 'Movie', component: Movie},
    {path: '/movieCopy/:id', name: 'MovieCopy', component: MovieCopy},
    {path: '/movieSimilar', name: 'MovieSimilar', component: MovieSimilar},
  ]
})

export default router
