<template>
  <div class="movie-intro-img">
    <img class="movie-intro-img-bg" :src="imageFullUrl + backdropPath" alt="" />
    <div class="movie-intro-img-bg_shdw"></div>
  </div>
  <div class="movie">
    <div class="movie-intro">
      <div class="movie-intro-text">
        <div class="movie-intro-text_img">
          <img
            class="movie-intro-text_img__item"
            :src="imageFullUrl + link"
            alt=""
          />
        </div>
        <div class="movie-intro-text_overview">
          <h1 class="movie-intro-text_overview__h1">{{ title }}</h1>
          <Genres
          :genres="genres"
          />
          <p class="movie-intro-text_overview---about">
            {{ overview }}
          </p>

          <Cast :id="this.id" @buttonClicked="clienterInformation"
          :type="'movie'"
          />
        </div>
      </div>
    </div>
  </div>

  <div class="movie-videos">
    <div class="movie-videos__wrapper container">
      <div
        class="movie-videos_item"
        v-for="(item, ind) in getMovieVideoArray"
        :key="ind"
      >
        <h2>{{ item.name }}</h2>
        <iframe class="movie-videos_item--video" :src="fullUrl + item.key">
        </iframe>
      </div>
      <h1 class="similarH1">Similar Movies</h1>
    </div>

    
    <Similar
    :id="this.id"
    />
    <Loading v-if="loadingTrueOrFalse == true" />
  </div>
</template>

<script>
import Loading from "@/components/Loading.vue";
import { mapGetters, mapActions } from "vuex";
import Similar from "@/components/Similar.vue";
import Cast from '@/components/Cast.vue';
import Genres from "@/components/Genres.vue";
export default {
  components: {
    Similar,
    Cast,
    Genres,
    Loading
  },
  data() {
    return {      
      componentKey: 0,
      loadingTrueOrFalse: false,
      imageUrl: "https://image.tmdb.org/t/p/w500",
      imageFullUrl: "https://image.tmdb.org/t/p/original",
      info: null,
      id: null,
      title: null,
      genres: null,
      overview: null,
      link: null,
      backdropPath: null,
      tagline: null,
      videosArray: null,
      videoItem: null,
      fullUrl: "https://www.youtube.com/embed/",
      typeMoT:'movie',
    };
  },
  computed: {
    ...mapGetters("movie", ["getMovieObj"]),
    ...mapGetters("videos", ["getMovieVideoArray"]),
    
  },
  methods: {
    ...mapActions("movie", ["getMovieId"]),
    ...mapActions("videos", ["getMovieVideo"]),
    clienterInformation() {
      let trueOrFalse = localStorage.getItem("loading");
      if (trueOrFalse == "true") {
        window.scrollTo({
          top: 0,
          behavior: "auto", // Можно использовать 'smooth', чтобы сделать плавную прокрутку
        });
        document.documentElement.style.overflow = "hidden";
        this.loadingTrueOrFalse = true;
      } else {
        this.loadingTrueOrFalse = false;
      }
    },
  },
  async mounted() {
    window.scrollTo({
      top: 0,
      behavior: "auto", // Можно использовать 'smooth', чтобы сделать плавную прокрутку
    });
    // Replace with the actual movie ID
    await this.getMovieId({movieId: this.id, type: this.typeMoT});
    this.info = this.getMovieObj;
    // console.log(this.info);
    this.title = this.info.title;
    this.genres = this.info.genres;
    this.overview = this.info.overview;
    this.link = this.info.poster_path;
    this.backdropPath = this.info.backdrop_path;
    this.tagline = this.info.tagline;
    ////////////////////////////////////////////////
   

    await this.getMovieVideo({movieId: this.id, type: this.typeMoT});
    this.videosArray = this.getMovieVideoArray;

    ////////////////////////////////////////////////

  },

  created() {
    var storedId = localStorage.getItem("movieSimilar");
    if (storedId != 0) {
      this.id = storedId;
    }
    
  },
};
</script>

  
<style lang="scss" scoped>
.similarH1{
  margin-top: 50px;
  margin-bottom: 25px;
  font-family: Poppins;
}
.swiper-wrapper {
  gap: 30px !important;
  display: flex;
  // align-items: center;
  // justify-content: center;
  flex-wrap: wrap;
}
.movie-videos_item {
  width: 100%;
  height: 900px;
  display: flex;
  flex-direction: column;
  gap: 25px;
  h2 {
    font-family: 'montserrat';
    font-weight: 400;
    line-height: 1.5;
    color: #fff;
  }
  // margin-top: 100px;
  &--video {
    padding: 20px;
    width: 100%;
    height: 100%;
    border: none;
    outline: none;
  }
}


body {
  background: #0f0f0f;
}
.movie {
  position: relative;
  margin-top: 150px;
  padding-bottom: 100px;
  width: 100%;
  height: 765px;
  overflow: hidden;
  &-videos {
    &__wrapper {
      // max-width: 1400px;
      // margin: 0 auto;

      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      gap: 100px;
    }
    // margin-top: 50px;
    padding: 25px 0 0 0;
    width: 100%;
    height: 100%;
    background: #000;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &-intro {
    width: 100%;
    height: 100%;

    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    &-img {
      width: 100%;
      height: 925px;
      display: flex;
      flex-direction: column;
      position: absolute;
      top: 0;
      z-index: 2;
      &-bg {
        width: 100%;
        height: 100%;
        object-fit: cover;
        position: relative;
        display: flex;

        &_shdw {
          width: 100%;
          height: 100%;
          background: linear-gradient(to top, rgb(0, 0, 0), rgba(0, 0, 0, 0.3));
          position: absolute;
          bottom: 0;
          left: 0;
          right: 0;
        }
      }
    }
    &-text {
      padding: 15px;
      position: absolute;
      top: 50px;
      z-index: 10;
      max-width: 1140px;
      width: 100%;
      gap: 20px;
      display: flex;
      align-items: flex-start;
      font-family: 'montserrat';
      color: #ffffff;
      &_img {
        width: 500px;
        display: flex;
        height: 600px;
        &__item {
          border-radius: 30px;
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
      &_overview {
        width: 65%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 30px;
        h1 {
          max-width: 805px;
          width: 100%;
          font-size: 2rem;
          line-height: 1;
          font-weight: 700;
        }
        &---genre {
          display: flex;
          gap: 10px;
          &___item {
            padding: 0.5rem 1.5rem;
            border: 2px solid #fff;
            border-radius: 30px;
            font-size: 0.8rem;
            font-weight: 600;
            background-color: #0f0f0f;
          }
        }
        &---about {
          max-width: 805px;
          width: 100%;
          font-weight: 400;
          line-height: 1.5;
          color: #fff;
        }
        
      }
    }
  }
}
.swiper {
  max-width: 600px;
  width: 100%;
  height: 300px;
  margin: 0 !important;
}
@media (max-width: 1200px) {
  .movie-videos_item {
    height: 600px;
  }
}
@media (max-width: 992px) {
  .movie-intro-text_overview{
    width: 100% !important;
  }
  .movie-intro-text_img {
    display: none;
  }
  .movie-videos_item {
    height: 400px;
  }
  .movie-videos_item--video {
    width: 100%;
    height: 100%;
  }
  .movie-intro-text_overview {
    &__h1 {
      max-width: 600px !important;
      width: 100%;
      font-size: 30px !important;
    }
  }
  .movie-videos__wrapper {
    gap: 50px !important;
  }
}
@media (max-width: 540px) {
  .movie-intro-text_img {
    display: none;
  }
  .movie-intro-text_overview {
    &__h1 {
      max-width: 400px !important;
      width: 100%;
      font-size: 30px !important;
    }
  }
}
</style>
