import axios from "axios";
export default {
  namespaced: true,
  state: {
    path: "https://api.themoviedb.org/3/tv/",
    movie: null,
  },
  getters: {
    getMovieVideoArray(state) {
      return state.movie;
    },
  },
  mutations: {
    getMovieVideo(state, payload) {
      state.movie = payload.splice(0, 5);
      // console.log(payload);
    },
  },
  actions: {
    async getMovieVideo({ state, commit, rootState }, movieId) {
      try {
        const res = await axios.get(
          `${state.path}${movieId}/videos?api_key=${rootState.apiKey}&language=en-US`
        );
        const result = res.data.results;
        commit("getMovieVideo", result);
        console.log(result);
      } catch (error) {
        console.log(error);
      }
    },
  },
};
