import axios from "axios"
export default {
  namespaced: true,
  state: {
    path: 'https://api.themoviedb.org/3/tv/',
    movie: null
  },
  getters: {
    getTvIdObject(state){return state.movie}
  },
  mutations: {
    getTvId(state, payload){
      state.movie = payload
    }
  },
  actions: {
    async getTvId({commit, state, rootState}, movieId){
      try{
        const res = await axios.get(`${state.path}${movieId}?api_key=${rootState.apiKey}&language=en-US`)
        const result = res.data
        commit('getTvId', result)
      }catch(err){
        console.log(err);
      }
    }
  },
}