<template>
  <div class="actor container">
    <div class="actor-profile">
      <div class="actor-profile__photo">
        <img :src="imgUrl + posterPath" alt="" />
      </div>
      <div class="actor-about">
        <h1 class="actor-about__name">{{ name }}</h1>
        <div class="actor-about__biography">
          <h3 class="actor-about__biography---title">Biography</h3>
          <div
            class="actor-about__biography---text"
            :class="{ loadedtext: !loadMore }"
          >
            <p
              class="actor-about__biography---text_p"
              v-for="(paragraph, index) in paragraphs"
              :key="index"
            >
              {{ paragraph }}
            </p>
          </div>
          <div class="actor-about__biography---text_loadmore" v-if="!loadMore">
            <button @click="loadText">Load More</button>
          </div>
        </div>
        <div class="wellKnown">
          <h3>Well known for</h3>
          <Swiper
            class="wellKnown-movies"
            :breakpoints="swiperOptions.breakpoints"
            :space-between="10"
            @swiper="onSwiper"
            @slideChange="onSlideChange"
          >
            <SwiperSlide
              v-for="(movie, ind) in actorMovies"
              :key="ind"
              class="wellKnown-movies_item"
            >
              <router-link
                to="/actormovie"
                class="wellKnown-movies_item--img"
                @click="getId(movie)"
              >
                <img :src="imgUrl + movie.poster_path" alt="" />
                <p>{{ movie.title }}</p>
              </router-link>
            </SwiperSlide>
          </Swiper>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from "swiper/vue";
import "swiper/scss";
import "swiper/scss/navigation";
import { mapActions, mapGetters } from "vuex";
export default {
  components: {
    Swiper,
    SwiperSlide,
  },
  data() {
    return {
      actorID: null,
      actorInfo: null,
      imgUrl: "https://image.tmdb.org/t/p/w500",
      posterPath: null,
      name: null,
      biography: null,
      loadMore: false,
      paragraphs: [],
      actorMovies: null,
      swiperOptions: {
        breakpoints: {
          320: { slidesPerView: 3 },
          576: { slidesPerView: 4 },
          768: { slidesPerView: 5 },
          992: { slidesPerView: 5 },
          1200: { slidesPerView: 6 },
        },
      },
    };
  },
  async mounted() {
    // console.log(this.actorID);
    this.actorID = localStorage.getItem("actorId");
    await this.getActor(this.actorID);
    this.actorInfo = this.getActorArray;
    this.posterPath = this.actorInfo.profile_path;
    this.name = this.actorInfo.name;
    this.biography = this.actorInfo.biography;

    if (this.biography) {
      const paragraphArray = this.biography.split("\n\n");
      const filteredParagraphs = paragraphArray.filter(
        (paragraph) => paragraph.trim() !== ""
      );
      this.paragraphs = filteredParagraphs;
    }

    // console.log(this.actorInfo);
    await this.getActorMovies(this.actorID);
    this.actorMovies = this.getActorMoviesArray;
  },
  methods: {
    ...mapActions("actor", ["getActor"]),
    ...mapActions("actor", ["getActorMovies"]),
    loadText() {
      this.loadMore = true;
    },
    getId(item) {
      localStorage.setItem("movieActorId", item.id);
    },
  },
  computed: {
    ...mapGetters("actor", ["getActorArray"]),
    ...mapGetters("actor", ["getActorMoviesArray"]),
  },
};
</script>

<style lang="scss" scoped>

.swiper{
  padding: 0 10px !important;
}
.wellKnown-movies_item--img {
  font-size: 0.9rem;
}
.loadedtext {
  overflow: hidden;
  text-overflow: ellipsis;
  height: 250px;
}
.actor {
  
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  margin-top: 150px;

  &-profile {
    width: 100% !important;
    display: flex;
    align-items: flex-start;
    font-family: 'montserrat';
    justify-content: center;
    gap: 20px;
    &__photo {
      max-width: 250px;
      // height: 300px;
      width: 100%;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
  &-about {
    
    width: 60%;
    display: flex;
    flex-direction: column;
    font-family: 'montserrat';
    color: white;
    &__name {
      font-weight: 700;
      color: #fff;
      font-size: 2.2rem;
    }
    &__biography {
     
      display: flex;
      flex-direction: column;
      position: relative;
      &---title {
        margin-top: 25px;
      }
      &---text {
        display: flex;
        gap: 20px;
        flex-direction: column;
      
        &_p {
          margin-bottom: 0;
          padding-bottom: 22px;
          line-height: 22px;

          // position: relative;
        }
        &_loadmore {
          align-self: flex-end;
          height: 40px;
          bottom: 0;
          // border-radius: 40px;
          right: 0;
          width: 100%;
          text-align: end;
          display: flex;
          align-items: flex-end;
          justify-content: flex-end;
          background: transparent;
          button {
            max-width: 100px;
            color: #fff;
            cursor: pointer;
            width: 100%;
            border: none;
            outline: none;
            font-size: 18px;
            background: transparent;
            position: absolute;
            &:hover {
              color: #18d95f;
              // color: black;
            }
          }
        }
      }
    }
  }
}
p {
  color: white;
}
.wellKnown {
  // max-width: 1400px;
  margin-top: 50px;
  width: 100% !important;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  &-movies {
    width: 100%;
    display: flex;
    // flex-wrap: wrap;
    align-items: flex-start;
    gap: 5px;
    flex-direction: column;
    &_item {
      width: 100%;
      &--img {
        width: 100%;
        img {
          width: 100%;
        }
        P {
        }
      }
    }
  }
}

@media (max-width: 992px) {
  .actor-profile {
    flex-direction: column;
    align-items: center;

    justify-content: center;
    width: 100% !important;
  }
  .actor-about__biography---text_p {
    font-size: 15px;
  }
  .actor-about__name {
    font-size: 1.5rem;
  }
  .wellKnown-movies_item {
    p {
      font-size: 10px;
    }
  }
  .actor-profile__photo {
    max-width: 320px;
  }
  .actor-about {
    width: 100%;
  }
}
</style>