<template>
<button @click="goOut" class="btn-more">

    <fa icon="fa-solid fa-bars"/>
    <span>More Info</span>
  
</button>


</template>

<script>
export default {
  props: ['page', 'id', 'type' ],
  data() {
    return {
      typeOf: "movie",
    }
  },
  methods: {
    goOut(){
      if(this.type == this.typeOf){
        localStorage.setItem('movieId', this.id)
      }else{
        localStorage.setItem('tvId', this.id)
      }
      setTimeout(() => {
        if(this.page == "movie"){
          this.$router.push(`/movie/${this.id}`);
        }else{
          this.$router.push(`/series/${this.id}`);
        }
      },  500);
    }
  },
}
</script>
<style lang="scss">
  .btn-more{
    cursor: pointer;
  }
</style>