<template>
  <div class="container main-content">
    <h1 class="main-content__title">
      {{ content == 'movie' ? 'All movies' : 'All Tv Series' }}
    </h1>
    <div class="main-content__list">
      <div class="main-content__card" v-for="(item, ind) in allList" :key="ind">
        <img :src="getFullImage + item.poster_path" alt="" class="main-content__card--img">
        <p class="tvname">{{ item.name }}</p>
        <router-link @click="getId(item)" :to="`/${type}/`+ item.id" class="main-content__card--link">{{item.title}}</router-link>
      </div>
    </div>
  </div>
  <button class="showmore" @click="showMoreBtn">Show More</button>
</template>

<script>
import { mapActions, mapGetters } from "vuex"
export default {
  props:['content', 'type'],
  data() {
    return {
      page: 1,
      loading: false,
      allList: [],
      id: null,
    }
  },
  computed: {
    ...mapGetters(['getFullImage', 'getImage'])
  },
  created() {
    
  },
  methods: {
    async getContent(){
      if(!this.loading){
        this.loading = true
        let action = this.content == 'movie' ? 'movies/getMovies' : 'tvs/getTv';
        await this.$store.dispatch(`${action}`, this.page)
        let getter = this.content == 'movie' ? 'movies/getMoviesArray' : 'tvs/getTvArray';
        const allInfo = await this.$store.getters[getter]
        this.allList.push(...allInfo)
        this.loading = false
        this.page++
      }
    },
    showMoreBtn() {
      window.scrollBy({
        top: + 200,
        behavior: "smooth",
      });
      this.getContent()
    },
    getId(item){
      if(this.type == 'movie'){
        localStorage.setItem('movieId', item.id)
      }else{
        localStorage.setItem('tvId', item.id)
      }
    }
  },
  mounted() {
    
    window.addEventListener('scroll', () => {
      if(window.scrollY + window.innerHeight >= document.body.scrollHeight){
        this.getContent();
      }
    })
    this.getContent()
  },
}
</script>
<style>
.tvname{
  color: white;
  font-family: 'montserrat';
  position: absolute;
  bottom: 20px;
  left: 0;
  z-index: 25;
  width: 100%;
  padding: 0 10px;
}
.showmore {
  width: 100%;
  padding: 20px;
  border: none;
  margin: 0 auto;
  background: #555555;
  color: white;
  z-index: 100;
  margin-top: 50px;
}
</style>