import axios from "axios"
export default {
  namespaced: true,
  state: {
    path: 'https://api.themoviedb.org/3/tv/',
    tv: null
  },
  getters: {
    getTvSerArray(state){return state.tv}
  },
  mutations: {
    getTvSer(state, payload){
      state.tv = payload
    }
  },
  actions: {
    async getTvSer({commit, state, rootState}, page = 1,movieId){
      try{
        const res = await axios.get(`${state.path}${movieId}/season/${page}?api_key=${rootState.apiKey}&language=en-US&page=${page}`)
        const result = res.data
        console.log(result);
        commit('getTvSer', result)
      }catch(err){
        console.log(err);
      }
    }
  },
}