<template>
  <div class="similar">
    
    <Swiper
      class="movie-similar container1"
      :breakpoints="swiperOptions.breakpoints"
      :space-between="15"
      @swiper="onSwiper"
      @slideChange="onSlideChange"
    >
      <SwiperSlide
        class="movie-similar_item"
        v-for="(movie, index) in similar"
        :key="index"
        @click="getSimilarId(movie)"
      >
        <router-link :to="'/movieSimilar'">
          <img :src="imageUrl + movie.poster_path" alt="" />
        </router-link>
      </SwiperSlide>
    </Swiper>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from "swiper/vue";
import "swiper/scss";
import { mapActions, mapGetters } from "vuex";
export default {
  props: ["id"],
  components: { Swiper, SwiperSlide },
  data() {
    return {
      similar: null,
      componentKey:0,
      similarId:null,
      imageUrl: "https://image.tmdb.org/t/p/w500",
      imageFullUrl: "https://image.tmdb.org/t/p/original",
      swiperOptions: {
        breakpoints: {
          320: { slidesPerView: 2 },
          576: { slidesPerView: 3 },
          768: { slidesPerView: 4 },
          992: { slidesPerView: 5 },
          1200: { slidesPerView: 8 },
        },
      },
    };
  },
  computed: {
    ...mapGetters("similar", ["getSimilarArray"]),
  },
  methods: {
    ...mapActions("similar", ["getSimilar"]),
    getSimilarId(item){
      this.similarId = item.id
      // console.log(this.similarId);
      localStorage.setItem('movieSimilar', this.similarId)
      window.location.reload()
    }
  },
  async mounted() {
    await this.getSimilar(this.id);
    this.similar = this.getSimilarArray;
  },
};
</script>

<style lang="scss" scoped>
.similar {
  width: 100%;
  padding: 0 20px;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
}
.swiper {
  max-width: 1400px !important;
  width: 100% !important;
  box-sizing: border-box;
  display: flex;
  align-items: flex-start;
}
.swiper-slide {
  // width: 500px !important;
}
.movie-similar {
  width: 100%;
  display: flex;
  align-items: flex-start;
  // justify-content: space-between;
  // height: 200px;
  cursor: pointer;

  gap: 20px;
  &_item {
    max-width: 200px;
    width: 100%;
    // margin: 0 25px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
.movie-similar {
  &_item {
    width: 100%;
  }
}
</style>